body,
html,
#SiteContainer,
.app {
    position: relative;
    height: 100%;
    width: 100%;
    margin: 0px;
}

body {
    background-color: $gn-light-grey;
}

a {
    color: inherit;
}

%box {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

%no-user-select {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@include OnEmbedDevice {
    .hide-on-embed,
    %hide-on-embed {
        display: none !important;
    }
}

@include OnMobileDevice {
    .hide-on-mobile,
    %hide-on-mobile {
        display: none !important;
    }
}

@include OnMobileLandscape {
    .hide-on-mobile-landscape,
    %hide-on-mobile-landscape {
        display: none !important;
    }
}

.deeplink-hidden {
    img {
        opacity: 0 !important;
    }
}
